import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import Row from "antd/lib/row"
import Button from "antd/lib/button"
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import {
  sendCmd
} from '../../../actions'
import Wifi from "../../icons/Wifi"
import CellBase from "../CellBase";
import {
  getMemoizeMotionPos,
  getMemoizeMotionState,
  getMemoizeSlaveAlState,
  getMemoizeSlaveOnlineState,
  getCurrentMotionVelPercent,
  getMemorizeDxl_connected,
  getMemorizeDxl_enabled,
  getMemorizeDxl_auto,
  getMemorizeDxl_normal
} from "../../../selectors";


class JogJoint extends CellBase {
  
  renderJogJoint = ({getPrefixCls}) => {
    
    const {
      prefixCls: customizePrefixCls,
      cell,
      slaveOnlineState,
      slaveAlState,
      motionState,
      motionPos,
      motionVelPercent,
      Dxl_connected,
      Dxl_enabled,
      Dxl_auto,
      Dxl_normal
    } = this.props

    
    // let options = this.getOptions()
    // console.log(options)
    // const jogList = options.jogList || 10
    // console.log(jogList)
    // const updateJogList = () =>{
    //   this.updateOptions({
    //     ...options,
    //     jogList: jogList + 1
    //   })
    // }
    // const deleteJogList = () =>{
    //   this.updateOptions({
    //     ...options,
    //     jogList: jogList - 1
    //   })
    // }
    const jogList = 10
    const prefixCls = getPrefixCls('jog-joint', customizePrefixCls);
    let list = []
    let DJlist = []
    for(let i = 0;i<jogList;i++){
      list.push(i)
    }
    if(list.length > 7){
      for(let i = 1;i<list.length-6;i++){
        DJlist.push(i)
      }
    }
      return <div
      className={`${prefixCls}`}
    > 
      {
      list.slice(0,7).map((i) => <Row key={i}>
          <Button
            type="primary"
            size={"large"}
            onClick={()=>{
              this.sendCmd(`j${i+1} --direction=-1 --vel_percent=${motionVelPercent}`, cell.i)
            }}
            onMouseDown={()=>{
              this.getOnMouseDownFn(
                `j${i+1} --direction=-1 --vel_percent=${motionVelPercent}`,
                cell.i
              )()
            }}
            onMouseUp={()=>{
              this.getOnMouseUpFn(`j${i+1} --stop`, cell.i)()
            }}
            onMouseOut={()=>{
              this.getOnMouseUpFn(`j${i+1} --stop`, cell.i)()
            }}
          >J{i+1}-</Button>
          <div className={`${prefixCls}-status`}>
            <span>
              {(motionPos && (motionPos[i] || motionPos[i] === 0))?(motionPos[i]/Math.PI*180).toFixed(3):'null'}
            </span>
            <div className={`${prefixCls}-status-icons`}>
              <Icon component={Wifi} className={`${
                (!slaveOnlineState || !slaveOnlineState[i])?`${prefixCls}-slave-online-state disabled`:`${prefixCls}-slave-online-state ${prefixCls}-slave-online-state-${slaveAlState && slaveAlState[i]}`
              }`}/>
              {
                !!(motionState && motionState[i]) && <Icon type="check-circle" theme={"filled"} className={"success"} />
              }
             
              {
                 !!(!motionState || !motionState[i]) && <Icon type="check-circle" theme={"filled"} className={"disabled"}/>
              }
            </div>
          </div>
          <Button
            type="primary"
            size={"large"}
            onClick={()=> {
            this.sendCmd(`j${i+1} --direction=1 --vel_percent=${motionVelPercent}`, cell.i)
          }}
            onMouseDown={()=>{
              this.getOnMouseDownFn(
                `j${i+1} --direction=1 --vel_percent=${motionVelPercent}`,
                cell.i
              )()
            }}
            onMouseUp={()=>{
              this.getOnMouseUpFn(`j${i+1} --stop`, cell.i)()
            }}
            onMouseOut={()=>{
              this.getOnMouseUpFn(`j${i+1} --stop`, cell.i)()
            }}
          >J{i+1}+</Button>
        </Row>)
      }
      {/* {
        DJlist.map((i) => <Row key={i+7}>
              <Button
                type="primary"
                size={"large"}
                onClick={()=>{
                  this.sendCmd(`j${i} --direction=-1`, cell.i)
                }}
                onMouseDown={()=>{
                  this.getOnMouseDownFn(
                    `j${i} --direction=-1`,
                    cell.i
                  )()
                }}
                // onMouseUp={()=>{
                //   this.getOnMouseUpFn(`dj${i+7} --stop`, cell.i)()
                // }}
                // onMouseOut={()=>{
                //   this.getOnMouseUpFn(`dj${i+7} --stop`, cell.i)()
                // }}
              >DJ{i}-</Button>
              <div className={`${prefixCls}-status`}>
                <span>
                  {(motionPos && (motionPos[i+7] || motionPos[i+7] === 0))?(motionPos[i+7]/Math.PI*180).toFixed(3):'null'}
                </span>
                <div className={`${prefixCls}-status-icons`}>
                  <Icon component={Wifi} className={`${
                    (!slaveOnlineState || !slaveOnlineState[7])?`${prefixCls}-slave-online-state disabled`:`${prefixCls}-slave-online-state ${prefixCls}-slave-online-state-${slaveAlState && slaveAlState[7]}`
                  }`}/>
                  {
                    !!(motionState && motionState[7]) && <Icon type="check-circle" theme={"filled"} className={"success"} />
                  }
                 
                  {
                     !!(!motionState || !motionState[7]) && <Icon type="check-circle" theme={"filled"} className={"disabled"}/>
                  }
                </div>
              </div>
              <Button
                type="primary"
                size={"large"}
                onClick={()=> {
                this.sendCmd(`j7 --direction=1 --vel_percent=${motionVelPercent}`, cell.i)
              }}
                onMouseDown={()=>{
                  this.getOnMouseDownFn(
                    `j7 --direction=1 --vel_percent=${motionVelPercent}`,
                    cell.i
                  )()
                }}
                // onMouseUp={()=>{
                //   this.getOnMouseUpFn(`j7 --stop`, cell.i)()
                // }}
                // onMouseOut={()=>{
                //   this.getOnMouseUpFn(`j7 --stop`, cell.i)()
                // }}
              >DJ{i}+</Button>
            </Row>)
      } */}
      {/* <Button className={`${prefixCls}-addJogJoint`} size={"small"} shape={"round"} onClick={this.addButtonFunc = ()=>{
        updateJogList()
      }}></Button>
      <Button className={`${prefixCls}-deleteJogJoint`} size={"small"} shape={"round"} onClick={this.deleteButtonFunc = ()=>{
        deleteJogList()
      }}></Button> */}
    </div>
    
  }
     
  render(){
    return <ConfigConsumer>{this.renderJogJoint}</ConfigConsumer>
  }
}

JogJoint.propTypes = {
  cell: PropTypes.object.isRequired,
  sendCmd: PropTypes.func.isRequired,
  vel: PropTypes.number,
  motionPos: PropTypes.array,  // 
  slaveOnlineState: PropTypes.array,
  Dxl_connected: PropTypes.bool, //wifi 1 
  Dxl_enabled: PropTypes.bool, //  1 
  Dxl_auto: PropTypes.bool, 
  Dxl_normal: PropTypes.bool
}

const getJogJointState = createSelector(
  getCurrentMotionVelPercent,
  getMemoizeMotionPos,
  getMemoizeMotionState,
  getMemoizeSlaveOnlineState,
  getMemoizeSlaveAlState,
  getMemorizeDxl_connected,
  getMemorizeDxl_enabled,
  getMemorizeDxl_auto,
  getMemorizeDxl_normal,
  (motionVelPercent, motionPos, motionState, slaveOnlineState, slaveAlState, Dxl_connected,Dxl_enabled,Dxl_auto,Dxl_normal) => {
    return {
      motionVelPercent,
      motionPos,
      motionState,
      slaveOnlineState,
      slaveAlState,
      Dxl_connected,
      Dxl_enabled,
      Dxl_auto,
      Dxl_normal
    }
  }
)

function mapStateToProps(state) {
  return getJogJointState(state)
}

const ConnectedJogJoint = connect(
  mapStateToProps,
  {
    sendCmd,
  },
)(JogJoint)

ConnectedJogJoint.NAME = 'Jog'
// ConnectedJogJoint.CONFIG = Config

export default ConnectedJogJoint

