import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import Row from "antd/lib/row"
import Button from "antd/lib/button"
import { connect } from 'react-redux'
import { createSelector } from 'reselect'
import {
  sendCmd
} from '../../../actions'
import Wifi from "../../icons/Wifi"
import CellBase from "../CellBase";
import {
  getMemoizeMotionPos,
  getMemorizeDxl_connected,
  getMemorizeDxl_enabled,
  getMemorizeDxl_auto,
  getMemorizeDxl_normal
} from "../../../selectors";
import { VerticalFlyout } from 'blockly';



class SteeringJogJoint extends CellBase {
  
  renderJogJoint = ({getPrefixCls}) => {
    
    const {
      prefixCls: customizePrefixCls,
      cell,
      motionState,
      motionPos,
      motionVelPercent,
      Dxl_connected,
      Dxl_enabled,
      Dxl_auto,
      Dxl_normal
    } = this.props

    const jogList = 10
    const prefixCls = getPrefixCls('jog-joint', customizePrefixCls);
    let list = []
    let DJlist = []
    for(let i = 0;i<jogList;i++){
      list.push(i)
    }
    if(list.length > 7){
      for(let i = 1;i<list.length-6;i++){
        DJlist.push(i)
      }
    }
      return <div
      className={`${prefixCls}`}
    > 
      {
        DJlist.map((i) => <Row key={i+7}>
              <Button
                type="primary"
                size={"large"}
                onClick={()=>{
                  this.sendCmd(`dj${i} --direction=-1`, cell.i)
                }}
                onMouseDown={()=>{
                  this.getOnMouseDownFn(
                    `dj${i} --direction=-1`,
                    cell.i
                  )()
                }}
                onMouseUp={()=>{
                  this.getOnMouseUpFn(`dj${i} --stop`, cell.i)()
                }}
                onMouseOut={()=>{
                  this.getOnMouseUpFn(`dj${i} --stop`, cell.i)()
                }}
              >DJ{i}-</Button>
              <div className={`${prefixCls}-status`}>
            <span>
              {(motionPos && (motionPos[i+6] || motionPos[i+6] === 0))?(motionPos[i+6]).toFixed(3):'null'}
            </span>
            </div>
              <Button
                type="primary"
                size={"large"}
                onClick={()=> {
                this.sendCmd(`dj${i} --direction=1`, cell.i)
              }}
                onMouseDown={()=>{
                  this.getOnMouseDownFn(
                    `dj${i} --direction=1`,
                    cell.i
                  )()
                }}
                onMouseUp={()=>{
                  this.getOnMouseUpFn(`dj${i} --stop`, cell.i)()
                }}
                onMouseOut={()=>{
                  this.getOnMouseUpFn(`dj${i} --stop`, cell.i)()
                }}
              >DJ{i}+</Button>
            </Row>)
      }
      {
        <div className={`${prefixCls}-status`}> 
        <div className={`${prefixCls}-status-icons`}>
        <span style={{fontSize:"30px",position:"relative",top:"65px"}}>
          {!!(Dxl_connected && Dxl_connected == 1) && <Icon type="wifi" style={{fontSize:"30px",position:"relative",right:"5px"}} className={"success"}/>}
          {!!(!Dxl_connected) && <Icon type="wifi" style={{fontSize:"30px",position:"relative",right:"5px"}} className={"danger"}/>}
          {
            !!(Dxl_enabled && Dxl_enabled == 1) && <Icon type="check-circle" theme={"filled"} className={"success"} style={{position:"relative",left:"5px"}}/>
          }
          {
             !!(!Dxl_enabled) && <Icon type="check-circle" theme={"filled"} className={"disabled"} style={{position:"relative",left:"5px"}}/>
          }  
          </span>
          <div style={{width:"100px",position:"relative",top:"80px",right:"17px"}}>
          <div style={{fontSize:"15px",float:"left"}}>
          {
             !!(Dxl_enabled && Dxl_enabled == 1) && !!(Dxl_auto != 1) && <span><font color="#52c41a"></font></span>
          }
          {
             !!(Dxl_enabled && Dxl_enabled == 1) && !!(Dxl_auto == 1) && <span><font color="#52c41a"></font></span>
          }
          </div>
          <div style={{fontSize:"15px",float:"right"}}>
          {
            !!(Dxl_enabled && Dxl_enabled == 1) && !!(Dxl_normal && Dxl_normal == 1) && <Icon type="check-circle" className={"success"}/>
          }
          {
            !!(Dxl_enabled && Dxl_enabled == 1) && !!(Dxl_normal && Dxl_normal == 1) && <span><font color="#52c41a"></font></span>
          }
          {
            !!(Dxl_enabled && Dxl_enabled == 1) && !!(Dxl_normal != 1) && <Icon type="stop" style={{color:"red"}}/>
          }
          {
            !!(Dxl_enabled && Dxl_enabled == 1) && !!(Dxl_normal != 1) && <span><font color="#FF0000"></font></span>
          }
          </div>
          </div>
        </div>
      </div>
      }
    </div>   
  }
     
  render(){
    return <ConfigConsumer>{this.renderJogJoint}</ConfigConsumer>
  }
}

SteeringJogJoint.propTypes = {
  cell: PropTypes.object.isRequired,
  sendCmd: PropTypes.func.isRequired,
  vel: PropTypes.number,
  motionPos: PropTypes.array,  // 
  Dxl_connected: PropTypes.bool, //wifi 1 
  Dxl_enabled: PropTypes.bool, //  1 
  Dxl_auto: PropTypes.bool, 
  Dxl_normal: PropTypes.bool
}

const getSteeringJogJointState = createSelector(
  getMemoizeMotionPos,
  getMemorizeDxl_connected,
  getMemorizeDxl_enabled,
  getMemorizeDxl_auto,
  getMemorizeDxl_normal,
  ( motionPos, Dxl_connected,Dxl_enabled,Dxl_auto,Dxl_normal) => {
    return {
      motionPos,
      Dxl_connected,
      Dxl_enabled,
      Dxl_auto,
      Dxl_normal
    }
  }
)

function mapStateToProps(state) {
  return getSteeringJogJointState(state)
}

const ConnectedSteeringJogJoint = connect(
  mapStateToProps,
  {
    sendCmd,
  },
)(SteeringJogJoint)

ConnectedSteeringJogJoint.NAME = 'Jog'
// ConnectedJogJoint.CONFIG = Config

export default ConnectedSteeringJogJoint

